import { gsap } from "gsap/dist/gsap";
import { ScrollTrigger } from "gsap/dist/ScrollTrigger";
import { ScrollToPlugin } from "gsap/dist/ScrollToPlugin";
import { CSSRulePlugin } from "gsap/dist/CSSRulePlugin";
import { Draggable } from "gsap/Draggable";


//import { SplitText } from "gsap/SplitText";

//gsap.registerPlugin(SplitText);
gsap.registerPlugin(ScrollTrigger);
gsap.registerPlugin(ScrollToPlugin);
gsap.registerPlugin(CSSRulePlugin);
gsap.registerPlugin(Draggable);


//SCROLL TO

window.scrollSection = function (a){
  gsap.to(window, {duration: 2, scrollTo: a});
}


//NAV VISIBLE

var bnr_anim = gsap.timeline();

bnr_anim.from("#js-nav", {opacity: 0, duration: .5, delay: .8})
//.from(".text-anim-bnr .row > span", {y: "190%", ease: "expo.out", duration: 0.5, stagger: 0.1}, "+=.3")



window.onload = function(){
   bnr_anim.play();
}


//MENU OPEN AND CLOSE

var mnu = 1;

document.querySelector(".nav_mobile_btn").addEventListener("click", mob_nav);
var close_nav = document.querySelectorAll(".js_close_nav");

close_nav[0].addEventListener("click", mob_nav);
close_nav[1].addEventListener("click", mob_nav);
close_nav[2].addEventListener("click", mob_nav);

function mob_nav(){
  if(mnu == 1){
    document.querySelector("#nav_mobile").setAttribute("class", "nav_mobile-visible");
    document.querySelector("#js-menu").setAttribute("class", "open");
    mnu = 0;
  }
  else if(mnu == 0){
    document.querySelector("#nav_mobile").setAttribute("class", "nav_mobile-hidden");
    document.querySelector("#js-menu").setAttribute("class", "");
    mnu = 1;
  }
}

//TEXT ANIMATION

gsap.utils.toArray(".o-text").forEach(section => {
  gsap.from(section.querySelectorAll(".o-row > span"), {
    scrollTrigger: section,
    y: "190%",
    //scaleY: 2.5,
    //rotate: 5,
    ease: "expo.out",
    duration: 0.4,
    delay: .5,
    stagger: 0.1
  })
});

gsap.utils.toArray(".o-text_wait").forEach(section => {
  gsap.from(section.querySelectorAll(".o-row > span"), {
    scrollTrigger: section,
    y: "190%",
    //scaleY: 2.5,
    //rotate: 5,
    ease: "expo.out",
    duration: 0.4,
    delay: 1.6,
    stagger: 0.1
  })
});


//BOX BORDERS

gsap.utils.toArray(".o-box").forEach(section => {
  gsap.to(section.querySelectorAll(".o-box__top, .o-box__bottom"), {
    scrollTrigger: {
      trigger: section,
      onEnter: () => ScrollTrigger.refresh()
    },
    scaleX: 1,
    //ease: "expo.out",
    duration: .8,
    //stagger: 0.1
  })
});

gsap.utils.toArray(".o-box").forEach(section => {
  gsap.to(section.querySelectorAll(".o-box__left, .o-box__right"), {
    scrollTrigger: section,
    scaleY: 1,
    ease: "expo.out",
    duration: 1,
    delay: .8
    //stagger: 0.1
  })
});

gsap.utils.toArray(".o-box").forEach(section => {
  gsap.to(section.querySelectorAll(".video-cover"), {
    scrollTrigger: section,
    opacity: 0,
    ease: "expo.out",
    duration: .6,
    delay: 2
    //stagger: 0.1
  })
});


//DRAGABLE SLIDER

 var ww = window.innerWidth;
 var dragBox = document.querySelector(".process-step").offsetWidth,
     boundLeft = ww - (dragBox * 1); // take the width of the BOX

     console.log(boundLeft);

Draggable.create(".js-drag", {
  type:"x",
  bounds: {minX:0, maxX:-boundLeft},
  //inertia: true
});


//VIDEO PLAY AND PAUSE

gsap.utils.toArray(".js-vid").forEach(section => {
  gsap.from(section.querySelector("video"), {
    scrollTrigger: {
      trigger: section,
      start: 'top bottom',
      end: 'bottom top',
      //markers: true,
      onEnter: () => {section.querySelector("video").play(); section.querySelector("video").setAttribute("loop", 1);},
      onEnterBack: () => {section.querySelector("video").play(); section.querySelector("video").setAttribute("loop", 1);},
      onLeave: () => {section.querySelector("video").pause(); section.querySelector("video").removeAttribute("loop");},
      onLeaveBack: () => {section.querySelector("video").pause(); section.querySelector("video").removeAttribute("loop");},
    },
  })
});